.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 10s linear;
          animation: App-logo-spin infinite 10s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}
/* 
@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
} 
*/

.btn-service-control {
  margin: 20px 20px 20px 0;
  padding: 10px 15px;
  border-radius: 2px;
}

.serverStatus{
  height:40px;
}

div#serviceConsole {
  text-align:left;
  height: 250px;
  overflow-y: scroll;
  overflow-x: auto;
  border: 2px solid #e0e0e0;
}


